@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
/* font-family: 'Dancing Script', cursive; */

* {
    box-sizing: border-box;
}

body {
    padding: 0;
    margin: 0;
    font-family: 'Dancing Script', cursive !important;
    color: #eee;
}

.title {
    color: #eee;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    align-self: initial;
    margin-top: -30px;
}

.title p {
    font-size: 18px;
}

.title h3 {
    font-size: 80px;
    margin: 0;
    width: 100%;
    text-align: center;
}

.title h1 {
    margin: 0 !important;
    font-size: 100px;
    text-align: center;
}

.App {
    height: 100vh;
    width: 100vw;
    background: #eee;
    margin: 0;
    padding: 0;
    background-image: url(/static/media/bg01.e2204767.jpg);
    z-index: 1;
    position: fixed;
}

.hover {
    width: 100vw;
    z-index: 100;
    display: flex;
    flex: 1 0 auto;
    height: 100vh;
    position: fixed;
    overflow-y: auto;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(0, 0, 0, .6);
    padding-bottom: 30px;
    padding-top: 30px;
}

.flex-start {
    justify-content: flex-start;
}

.card-pedido {
    padding: 15px 35px;
    background-color: rgba(0, 0, 0, .9);
    border: 1px solid rgba(55, 55, 55, .4);
    border-radius: 2px;
    width: 500px;
    margin-top: 30px;
    flex-direction: column;
    display: -ms-flexbox;
}

.card-pedido input {
    outline: none;
    border: none;
    border-bottom: 1px solid #eee;
    background: rgba(0, 0, 0, .4);
    padding: 4px 10px;
    margin-bottom: 30px;
    color: #eee;
    font-size: 18px;
    width: 100%;
    border-radius: 2px;
}

button.btn-envio-pedido {
    background-color: rgba(180, 30, 30, .7);
    border: 1px solid rgba(140, 30, 30, .8);
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    padding: 7px 20px;
    border-radius: 2px;
    outline: none;
    color: #eee;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    transition: 200ms;
}

button.btn-envio-pedido:hover {
    background-color: rgba(140, 30, 30, .7);
    border: 1px solid rgba(80, 30, 30, .8);
}

.btn-resolver-pedido {
    margin-top: 15px;
    background-color: rgba(30, 180, 30, .7);
    border: 1px solid rgba(30, 140, 30, .8);
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    padding: 3px 20px;
    border-radius: 2px;
    outline: none;
    color: #eee;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    transition: 200ms;
    display: flex;
    align-items: center;
    text-decoration: none;
}

.btn-resolver-pedido:hover {
    background-color: rgba(30, 140, 30, .7);
    border: 1px solid rgba(30, 80, 30, .8);
}

.disabled {
    background-color: rgba(70, 70, 70, .4) !important;
    border-color: rgba(70, 70, 70, .4) !important;
}

button svg,
a svg {
    font-size: 20px;
    margin-right: 5px;
}


.caixa {
    width: 100%;
    min-width: 100%;
    min-height: 150px;
    background: none;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, .5);
    color: #eee;
    font-family: 'Dancing Script', cursive !important;
    font-size: 25px;
}

.caixa::-webkit-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #eee;
    opacity: 1;
    /* Firefox */
}

.caixa:-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #eee;
    opacity: 1;
    /* Firefox */
}

.caixa::-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #eee;
    opacity: 1;
    /* Firefox */
}

.caixa::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #eee;
    opacity: 1;
    /* Firefox */
}

.caixa:focus {
    outline: none;
}

.show-numero-pedido {
    color: #eee;
}

.card-pedido {
    border: 1px solid rgba(80, 30, 30, .8);
    background-color: rgba(0, 0, 0, .7);
    border-radius: 3px;
}

.card-header {
    text-align: center;
    font-size: 24px;
    color: rgba(230, 30, 30, .7);
}

.card-body {
    font-size: 20px;
}

.card-footer {
    margin-top: 15px;
    border-top: 1px solid #eee;
    display: flex;
}

input.form-control {
    width: 100%;
    border: none;
    border-bottom: 1px solid #eee;
    border-radius: 3px;
    background-color: rgba(170, 170, 170, .4);
    padding: 7px 15px;
    outline: none;
    color: #111;
    font-size: 20px;
    text-align: center;
}

input.form-control:focus {
    outline: none;
}

input.form-control::-webkit-input-placeholder {
    color: #222;
}

input.form-control:-ms-input-placeholder {
    color: #222;
}

input.form-control::-ms-input-placeholder {
    color: #222;
}

input.form-control::placeholder {
    color: #222;
}

#login-input {
    display: flex;
    align-self: center;
    padding: 8px 10px;
    border: none;
    outline: none;
    font-size: 20px;
    border-radius: 3px;
    background-color: rgba(0, 0, 0, .5);
    color: #eee;
}

#login-btn {
    display: flex;
    align-items: center;
    padding: 4px 15px;
    border: 1px solid rgba(30, 140, 30, .6);
    background-color: rgba(30, 180, 30, .8);
    border-radius: 3px;
    margin-top: 15px;
    transition: 200ms;
    cursor: pointer;
}

#login-btn:hover {
    border: 1px solid rgba(30, 80, 30, .6);
    background-color: rgba(30, 140, 30, .8);
}

#login-btn svg {
    margin-right: 10px;
}

@media(max-width: 1480px) {
    .App {
        background-position: 50%;
        background-size: cover;
    }
}

@media(max-width: 780px) {
    .title {
        padding: 0 15px;
    }

    .title h1 {
        font-size: 60px;
    }

    .title h3 {
        font-size: 40px;
    }

    .card-pedido {
        width: 80%;
    }
}

@media(max-width: 375px) {
    .title h1 {
        font-size: 50px;
    }

    .title h3 {
        font-size: 30px;
    }

    .card-pedido {
        width: 90%;
    }
}
